  }

/*search bar*/

.searchBox {
    /*position: center;*/
    /*top: 50%;
    left: 50%;*/
    /* transform:  translate(0%,0%); */
    background: #f1f1f177;
    height: 30px;
    border-radius: 30px;
    padding: 10px;
    align-self: center;

}

.searchBox:hover > .searchInput {
    width: 240px;
    padding: 0 6px;
}

.searchBox:hover > .searchButton {
  background: rgba(255, 255, 255, 0);
  color : #d5d8dd;
}

.searchButton {
    color: rgba(8, 8, 8, 0.678);
    /*float: right;*/
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: #00030700;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.4s;
    top: 50%;
}

.searchInput {
    border:none;
    background: none;
    outline:none;
    float:left;
    padding: 0;
    color: #75B1A9;
    font-size: 16px;
    transition: 0.4s;
    line-height: 40px;
    width: 0px;

}

@media screen and (max-width: 620px) {
.searchBox:hover > .searchInput {
    width: 150px;
    padding: 0 6px;
}
}

/*L'art de la chapellerie*/

h1 {
    font-family: 'Calligraffitti', cursive;
    font-weight: 260;
    font-size: 350%;
    margin-top: 4rem;
    margin-left: 6rem;
    position: relative;
}
h2 {
    font-family: 'rosario';
    font-weight: 400;
    font-size: 150%;
    margin-left: 6rem;
    color: #AAA;
    margin-top: 40px;
}

.oval {
    height: 70px;
    width: 510px;
    background-color: rgba(100, 100, 100, 0);
    border-color: black;
    border-radius: 50%;
    position: absolute;
    top: 260px;
    margin-left: 2rem;
    border: 2.1px solid rgba(3, 3, 3, 0.76);
  }

  .chapeau {
    border-width: 2px 4px;
    border-radius: 170px 170px 170px;
    margin-left: 800px;
    top: 240px;
    width: 350px;
    height: 550px;
    position: absolute;
    object-fit: cover;
    object-position: 20% 0;
  }